<div class="container"><div class="form-group linkBack"><router-link to="/"><img class="me-2" src="@/assets/images/back.svg">Главная</router-link></div><div class="row"><div class="col-12 d-flex justify-content-center"><div class="loadMain"><h3>Загрузить файл</h3><p>Формат файла .xlsx</p><a class="btn w-100 text-center mb-3" href="/CompanyExample.xlsx" download>Скачать пример файла</a><label class="btnSecondary w-100 text-center" for="fileLoad">Загрузить файл</label><input class="d-none" id="fileLoad" type="file" @change="$_vision_load_getFile"></div></div><div class="d-flex justify-content-center mt-5" v-if="spinner"><div class="spinner-border text-light"></div></div><div v-if="result.load"><h3 class="mb-3 mt-5">Полученный результат</h3><div class="loadResult"><div class="row"><div class="col-4 d-flex text-center flex-column"><h5>Найдено компаний</h5><p>{{result.all}}</p></div><div class="col-4 d-flex text-center flex-column"><h5>Верных отраслей ({{Math.floor(result.countTrueIndustries * 100 / result.all)}} %)</h5><p>{{result.countTrueIndustries}}</p></div><div class="col-4 d-flex text-center flex-column"><h5>Верных технологий ({{Math.floor(result.countTrueTechnology * 100 / result.all )}} %)</h5><p>{{result.countTrueTechnology}}</p></div></div></div></div></div></div>